@import url(https://fonts.googleapis.com/css2?family=Ubuntu);
html {
  max-width: 100vw;
  overflow-x: hidden;
  box-sizing: border-box;
  font-size: 16px;
}

body {
  max-width: 100vw;
  overflow: auto;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  background: #e5e5e5;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

@media only screen and (min-width:2000px) {
  .projects {
    margin-right: 30%
  }
  .aboutContainer {
    margin-left: 30%
  }
  
  .workContainer {
    grid-column: 3 / 6;
  }

  .eduContainer {
    grid-column: 7 / 10
  }
}

/*HEADER AND IT'S CONTENTS*/

header {
  grid-column: 1 / span 12;
  font-family: Ubuntu, sans-serif;
  background-image: linear-gradient(white, black, white),
    url("/img/MyPic-min.png");
  background-blend-mode: saturation;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  color: #d8d7d4;
  display: flex;
  flex-flow: column nowrap;
  height: 100vh;
  text-align: center;
}

.signature {
  color: #14213d;
  margin-top: 3em;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-left: auto;
  margin-right: auto;
}

.titleAndSubtitle {
  -webkit-filter: none;
          filter: none;
  text-align: left;
  margin: 4vw;
  padding: 1.5em;
  margin-top: auto;
  margin-bottom: auto;
  word-wrap: break-word;

  max-width: 33%;
  border-radius: 30px;
  background: rgba(0, 0, 0, 0.65);
}

.heroLink {
  margin: 0.5em;
}

.heroLink  img{
  width: 2.5em
}

.title {
  text-shadow: 2px 2px 2px black;
  color: #f4f3ee;
  font-size: 4em;
}

.subtitle {
  color: #fca311;
  text-shadow: 2px 2px 2px black;
  padding-top: 1em;
  padding-bottom: 0.5em;
}

nav {
  position: fixed;
  display: flex;
  z-index: 1;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  width: 100vw;
}

.navlink {
  background-color: rgb(52, 58, 62);
  color: #fca311;
  width: 20%;
  text-decoration: none;
  font-size: 1.3em;
  padding-top: 0.3em;
  padding-bottom: 0.3em;
  text-align: center;
  transition: 0.3s ease-in-out;
}

.navlink:not(:last-child) {
  border-right: none;
}

.navlink:hover {
  background-color: #fca311;
  color: #e5e5e5;
  width: 25%;
}

.navlink:active {
  background-color: white;
  color: #454140;
  transition: 0s;
}

/*Fade-in*/
.fade-in-hero {
  opacity: 0;
  transform: translateX(-20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-hero.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

@media only screen and (max-width: 1000px) {
  header {
    background-position: 50%;
  }

  .titleAndSubtitle {
    margin: auto 2vw;
    max-width: 100%;
    background: none;
  }

  .navlink:hover {
    width: 20%;
  }

  .heroLinkPic {
    max-width: 2em;
  }
}

@media only screen and (max-width: 550px) {
  header {
    height: 55vh;
    background-size: cover;
    background-position: 60% -20vh;
  }

  .navlink {
    font-weight: bolder;
    font-size: 1.3em;
    padding-left: 0;
    padding-right: 0;
  }

  .navText {
    display: none;
  }

  .title {
    font-weight: bold;
    font-size: 2em;
  }

  .subtitle {
    font-weight: bold;
    font-size: 1.1em;
  }

  .signature {
    word-wrap: break-word;
  }

}

/*About container*/
.aboutContainer {
  grid-column: 1 / span 4;
  grid-row: 2;
  text-align: center;
  padding: 2vh;
  color: #14213d;
}
.aboutHeader {
  text-shadow: 2px 2px #14213d;
  font-weight: bold;
  color: #fca311;
  font-size: 2em;
  margin: 1vh;
}

.aboutContainer p {
  margin: 1vh;
}

.aboutText {
  font-size: 1em;
}

.picOfMe {
  width: 40%;
  border-radius: 20px;
}

/*Fade in*/
.fade-in-about {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.5s ease-out;
  will-change: opacity, visibility;
}
.fade-in-about.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

@media only screen and (max-width: 1000px) {
  .aboutContainer {
    grid-column: 1 / span 12;
  }
}
@media only screen and (max-width: 550px) {
  .picOfMe {
    width: 50%;
  }
}

.outerProject {
  perspective: 1000px;
}

.innerProject {
  position: relative;
  width: 100%;
  max-height: 100%;
  transform-style: preserve-3d;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: rgb(52, 58, 65);
  box-shadow: 2px 2px 4px black;
  color: white;
  border: solid 0px black;
  border-radius: 5px;
}

.description {
  max-height: 100%;
  overflow-y: auto;
}

.projectFront {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.projectBack {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.innerProject:hover {
  background-color: #19395e;
  color: white;
  transition: 0.2s;
}

.projectBack {
  transform: rotateY(180deg);
  background: rgb(52, 58, 65);
  padding: 1em;
  padding-bottom: 4em;
}

.projectname {
  font-family: Ubuntu, Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  justify-self: flex-start;
  text-decoration: underline;
  display: block;
  overflow: visible;
  margin-bottom: 1em;
  font-size: 1.2em;
}

.projectname:hover {
  color: #fca311;
  text-decoration: underline;
}

.expandArrow {
  z-index: 2;
  rotate: -90deg;
  cursor: pointer;
  width: 10%;
  align-self: center;
  border-radius: 100vw;
  margin-bottom: 1em;

  transition: 0.2s;
}

.expandArrow:hover {
  background: #f3f3f3;
  box-shadow: #f3f3f3 0px 0px 10px;

  transition: 0.2s;
}

.projectThumbnail {
  object-fit: contain;
  display: block;
  height: 5em;
  margin: 1em auto 1em auto;
}

.outerProject.open .innerProject {
  transform: rotateY(180deg);
}

.fade-in-project {
  opacity: 0.3;
  visibility: hidden;
  transition: opacity 0.3s ease-out, transform 0.8s ease-out;
  will-change: opacity, visibility;
}

.fade-in-project.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
  transition: 0.5s;
  will-change: visibility, opacity;
}

@media only screen and (max-width: 1000px) {
  .innerProject {
    width: 100%;
  }

  .projectname {
    font-size: 1.5em;
  }

  .projectThumbnail {
    width: 40%;
  }

  .description {
    font-size: 1em;
    margin-bottom: 0em;
    text-overflow: ellipsis;
  }
}

@media only screen and (max-width: 550px) {
  .innerProject {
    min-width: 85vw;
    height: 100%;
  }
}

/*Projects*/
.projects {
  text-align: center;
  grid-column: 5 / span 12;
  grid-row: auto;
  margin-left: 2.5%;
  margin-right: 2.5%;
  padding-bottom: 2vh;
}

.projectsGrid {
  display: grid;

  grid-auto-flow: row dense;
  overflow-y: hidden;
  grid-template: 1fr 1fr / 1fr 1fr 1fr;
  grid-gap: 0.5em;
  padding: 0.5em;
  color: #d8d7d4;
}

.projectsHeader {
  text-shadow: 2px 2px #14213d;
  font-weight: bold;
  color: #fca311;
  font-size: 2em;
  margin: 1vh;
}

/*Fade-in*/
.fade-in-projects {
  opacity: 0;
  transform: translateX(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.5s ease-out;
  will-change: opacity, visibility;
}
.fade-in-projects.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

@media only screen and (max-width: 550px) {
  .projectsGrid {
    grid-auto-flow: column dense;
    grid-template-rows: 1fr;
    overflow-x: auto;
    height: auto;
  }
}

@media only screen and (max-width: 1000px) {
  .projects {
    grid-column: 1 / span 12;
    overflow-x: hidden;
  }
}

.job {
  text-align: center;
  margin: 1em;
  color: #fca311;
  background: rgb(52, 58, 65);
  border-radius: 15px;
  box-shadow: 3px 3px 3px black;
}

.jobTitle {
  padding: 0.75em;
  background: #fca311;
  color: #14213d;
  border-radius: 15px;
  max-width: 100%;
}

.jobCompany {
  border: 2px solid #fca311;
  padding: 0.25em 0.75em;
  margin: 0.25em auto 0.25em auto;
  border-radius: 50px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.jobStartAndEnd {
  color: white;
  padding: 0.5em;
  display: flex;
  justify-content: space-around;
}

.fade-in-job {
  opacity: 0;
  transform: translateX(-20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.25s ease-out;
  will-change: opacity, visibility;
}

.edu {
  background: #fca311;
}

.edu > .jobTitle {
  background: rgb(52, 58, 65);
  color: white;
}

.edu > .jobCompany {
  border-color: black;
  color: black;
  max-width: 80%;
  width:auto;
  word-wrap: break-word;
}

.fade-in-job.edu {
  transform: translateX(20vh);
}

.fade-in-job.is-visible {
  opacity: 1;
  transform: translateX(0);
  visibility: visible;
}

.workContainer {
  grid-column: 1 / span 6;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.workHeader {
  text-shadow: 2px 2px #14213d;
  font-weight: bold;
  color: #fca311;
  font-size: 2em;
  margin: 1vh;
}

.fade-in-work {
  opacity: 0;
  transform: translateX(-20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.25s ease-out;
  will-change: opacity, visibility;
}

.fade-in-work.is-visible {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

@media only screen and (max-width: 1000px) {
  .workContainer {
    grid-column: 1 / span 12;
  }
}

.eduContainer {
  grid-column: 7 / 12;
  overflow: hidden;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.fade-in-edu {
  opacity: 0;
  transform: translateX(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.25s ease-out;
  will-change: opacity, visibility;
}

.fade-in-edu.is-visible {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

@media only screen and (max-width: 1000px) {
  .eduContainer {
    grid-column: 1 / span 12;
    margin-left: auto;
    margin-right: auto;
  }
}

